export default {
  _get(k) {
    return localStorage.getItem(k)
  },
  _set(k, v) {
    return localStorage.setItem(k, v)
  },
  _getAsync(k) {
    return new Promise((resolve, reject) => {
      const res = localStorage.getItem(k)

      if (res) {
        resolve(res)
      }
      reject(res)
    })
  },
}


